/* Header */

.header-container {
  width: 100vw;
  height: 70px;
  background-color: #32a852;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.header-sub {
  display: flex;
  gap: 20px;
  align-items: center;
}

.header-buttons {
  font-size: 20px;
  font-weight: 200;
  color: #fff;
  text-decoration: none;
}

.header-buttons:hover {
  color: #000;
}

.header-title {
  font-size: 30px;
  font-weight: 200;
  color: #fff;
  text-decoration: none;
}

/* Index Page */
body {
  background-color: #f2f2f2;
}

.container {
  width: 100%;
  flex-direction: column;
  display: flex;
  align-items: center;
}

.inner-container {
  width: 90vw;
  max-width: 1000px;
}

.page-title {
  font-size: 30px;
  font-weight: 200;
  color: #000;
  text-decoration: none;
  margin: 20px 0;
}
.blog-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.blog-preview {
  width: 100%;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  border-radius: 10px;
}
.blog-title {
  font-size: 20px;
  font-weight: 200;
  color: #000;
  text-decoration: none;
}
.blog-date {
  font-size: 15px;
  font-weight: 200;
  color: #000;
  text-decoration: none;
}
.blog-author {
  font-size: 15px;
  font-weight: 200;
  color: #000;
  text-decoration: none;
}

.blog-preview:hover {
  background-color: #32a852;
  color: #fff;
  box-shadow: 0 0 10px 0.5 #000;
}

/* Blog */

.blog-subcontents {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.page-title {
  font-size: 40px;
  font-weight: 500;
  color: #000;
  text-decoration: none;
  margin: 20px 0;
}

.hero-image {
  width: 100%;
  height: 300px;
  background-color: #000;
  margin: 20px 0;
  object-fit: cover;
}
